<template>
  <v-expansion-panels v-model="panels">
    <v-expansion-panel class="py-2">
      <v-expansion-panel-header>
        <v-row no-gutters align="center">
          <v-col cols="4" class="text-h6"> Filters </v-col>
        </v-row>
      </v-expansion-panel-header>
      <v-expansion-panel-content>
        <v-row class="px-3" no-gutters>
          <v-col>
            <v-menu v-model="startMenu" :close-on-content-click="false" transition="scale-transition" offset-y min-width="auto" z-index="15">
              <template v-slot:activator="{ on: menuOn, attrs }">
                <v-tooltip top>
                  <template v-slot:activator="{ on: tooltipOn }">
                    <v-text-field v-model="startDate" label="Start Date" prepend-icon="mdi-calendar" readonly v-bind="attrs" v-on="{ ...menuOn, ...tooltipOn }"></v-text-field>
                  </template>
                  <span>Show reviews whose submission dates start at {{ startDate }}</span>
                </v-tooltip>
              </template>
              <v-date-picker
                v-model="startDate"
                @input="
                  () => {
                    this.startMenu = false;
                    this.changed = true;
                  }
                "
                no-title
              ></v-date-picker>
            </v-menu>
          </v-col>
          <v-col class="pl-4">
            <v-menu v-model="endMenu" :close-on-content-click="false" transition="scale-transition" offset-y min-width="auto" z-index="15">
              <template v-slot:activator="{ on: menuOn, attrs }">
                <v-tooltip top>
                  <template v-slot:activator="{ on: tooltipOn }">
                    <v-text-field v-model="endDate" label="End Date" prepend-icon="mdi-calendar" readonly v-bind="attrs" v-on="{ ...menuOn, ...tooltipOn }"></v-text-field>
                  </template>
                  <span>Show reviews whose submission dates end at {{ endDate }}</span>
                </v-tooltip>
              </template>
              <v-date-picker
                v-model="endDate"
                @input="
                  () => {
                    this.endMenu = false;
                    this.changed = true;
                  }
                "
                no-title
              ></v-date-picker>
            </v-menu>
          </v-col>
          <v-col cols="3" v-if="!disableReviewTypeFilter" class="pl-4">
            <v-tooltip top>
              <template v-slot:activator="{ on }">
                <div v-on="on">
                  <v-select
                    v-model="statusList"
                    prepend-icon="mdi-animation"
                    :items="reviewType"
                    small-chips
                    label="Review Types"
                    multiple
                    clearable
                    deletable-chips
                    @input="changed = true"
                  ></v-select>
                </div>
              </template>
              <span v-html="formattedTooltipText(reviewTypeTooltipText)"></span>
            </v-tooltip>
          </v-col>
          <v-col v-if="!disableReviewerFilter" cols="3" class="pl-4">
            <v-select v-model="reviewerLoginList" prepend-icon="mdi-account" :items="reviewers" small-chips label="Reviewers" multiple clearable deletable-chips @input="changed = true"></v-select>
          </v-col>
          <v-col v-if="!disableHistogramFilter" class="pl-4">
            <label>Data Interval</label>
            <v-slider ticks="always" tick-size="10" v-model="histogramIntervalFilter" :tick-labels="histogramIntervals" :max="3" step="1" @change="changed = true"></v-slider>
          </v-col>
        </v-row>
        <v-row no-gutters justify="space-between">
          <div class="d-flex pl-4" style="gap: 10px">
            <Button title="Last Year" @click="setLastOneUnit('years')" />
            <Button title="Last Month" @click="setLastOneUnit('months')" />
            <Button title="Last Week" @click="setLastOneUnit('weeks')" />
          </div>
          <Button title="Apply" :color="changed ? '#56b36e' : ''" :disabled="!changed" @click="applyFilters" />
        </v-row>
      </v-expansion-panel-content>
    </v-expansion-panel>
  </v-expansion-panels>
</template>

<script>
import moment from "moment";
import Button from "@/components/Button";
import { mapActions, mapGetters } from "vuex";
import { getArrayQueryParam } from "@/utils/routerUtil";
export default {
  components: { Button },
  props: {
    disableReviewerFilter: {
      type: Boolean,
      default: false,
    },
    disableReviewTypeFilter: {
      type: Boolean,
      default: false,
    },
    disableHistogramFilter: {
      type: Boolean,
      default: false,
    },
  },
  data: () => ({
    startMenu: false,
    endMenu: false,
    startDate: moment(new Date()).subtract(1, "months").format("YYYY-MM-DD"),
    endDate: moment(new Date()).format("YYYY-MM-DD"),
    statusList: [],
    reviewerLoginList: [],
    reviewers: [],
    histogramIntervalFilter: 0,
    panels: 0,
    changed: false,
  }),
  computed: {
    ...mapGetters(["repoUsers", "repoConfig"]),
    reviewType() {
      return ["FAULTY", "SUCCESSFUL"];
    },
    histogramIntervals() {
      return ["Daily", "Weekly", "Monthly", "Yearly"];
    },
    reviewTypeTooltipText() {
      return `&#8277; Successful reviews caught more than ${this.repoConfig.faultyThreshold}% of injected bugs\n
              &#8277; Faulty reviews caught less than ${this.repoConfig.faultyThreshold}% of injected bugs`;
    },
  },
  methods: {
    ...mapActions(["getRepoUsers"]),
    applyFilters() {
      let query = { ...this.$route.query };
      query.startDate = this.startDate;
      query.endDate = this.endDate;
      if (!this.disableReviewerFilter) {
        query.reviewers = this.reviewerLoginList;
      }
      if (!this.disableReviewTypeFilter) {
        query.reviewTypes = this.statusList;
      }
      if (!this.disableHistogramFilter) {
        query.histogramInterval = this.histogramIntervalFilter;
      }
      this.$router.replace({ query }).catch((err) => {
        // Ignore errors related to redirecting to the same url. It means filters did not change.
        if (err.name !== "NavigationDuplicated") {
          console.error(err);
        }
      });
      this.$emit("applyFilters", {
        startDate: moment(this.startDate).format(),
        endDate: moment(this.endDate).add(1, "day").format(),
        reviewerLoginList: this.reviewerLoginList,
        statusList: this.statusList,
        histogramInterval: this.histogramIntervals[this.histogramIntervalFilter].toUpperCase(),
        timezone: moment().format("Z"),
      });
      this.changed = false;
    },
    setLastOneUnit(unit) {
      this.changed = true;
      this.startDate = moment(new Date()).subtract(1, unit).format("YYYY-MM-DD");
      this.endDate = moment(new Date()).format("YYYY-MM-DD");
    },
    setInitialFilters() {
      this.reviewerLoginList = this.$route.params.username ? [this.$route.params.username] : getArrayQueryParam(this.$route.query.reviewers) || this.reviewerLoginList;
      this.statusList = getArrayQueryParam(this.$route.query.reviewTypes) || this.statusList;
      this.startDate = this.$route.query.startDate || this.startDate;
      this.endDate = this.$route.query.endDate || this.endDate;
      this.histogramIntervalFilter = this.$route.query.histogramInterval || this.histogramIntervalFilter;
    },
  },
  async mounted() {
    await this.getRepoUsers(this.$route.params.repoUrl);
    this.reviewers = this.repoUsers.map((user) => user.login);
    this.setInitialFilters();
    // Wait for histogramIntervalMs computed field to be ready
    this.$nextTick(() => {
      this.applyFilters();
    });
  },
};
</script>
