

























import MainLayout from "../components/MainLayout.vue";
import { FaultyReview } from "@/types/FaultyReview";
import { ReviewFiltersType } from "@/types/ReviewFiltersType";
import { ReviewOptions } from "@/types/ReviewOptions";
import Vue from "vue";
import { mapActions } from "vuex";
import moment from "moment";
import ReviewFilters from "../components/ReviewFilters.vue";

export default Vue.extend({
  name: "Reviews",
  components: { MainLayout, ReviewFilters },
  data: () => ({
    reviews: [],
    totalCount: 0,
    loading: true,
    options: {
      page: 1,
      itemsPerPage: 10,
      multiSort: true,
      sortBy: ["submittedAt"],
      sortDesc: [true],
    },
    sortBy: [{ field: "submittedAt", order: -1 }],
    start: moment(new Date()).subtract(1, "months").format("YYYY-MM-DD"),
    end: moment(new Date()).format("YYYY-MM-DD"),
    statusList: [],
    reviewerLoginList: [],
  }),
  methods: {
    ...mapActions(["getReviews"]),
    navigateToPullRequest(item: FaultyReview) {
      this.$router.push({
        path: `/${this.$route.params.repoUrl}/pulls/${item.pullNumber}`,
      });
    },
    formatDate(date: string) {
      return moment(date).format("DD-MM-YYYY HH:mm:ss");
    },
    async listFaultyReviews() {
      this.loading = true;
      const { data, totalCount } = await this.getReviews({
        repoUrl: this.$route.params.repoUrl,
        pageNo: this.options.page,
        pageSize: this.options.itemsPerPage,
        sortBy: this.sortBy,
        startDate: this.start,
        endDate: this.end,
        statusList: this.statusList,
        reviewerLoginList: this.reviewerLoginList,
      });
      this.reviews = data.map((obj: FaultyReview) => {
        obj.submittedAt = this.formatDate(obj.submittedAt);
        return obj;
      });
      this.totalCount = totalCount;
      this.loading = false;
    },
    optionsUpdated(options: ReviewOptions) {
      if (options.sortBy.length === 0) {
        this.sortBy = [{ field: "submittedAt", order: -1 }];
      } else {
        this.sortBy = options.sortBy.map((value: string, i: number) => {
          return { field: value, order: options.sortDesc[i] === true ? -1 : 1 };
        });
      }
      this.listFaultyReviews();
    },
    applyFilters(args: ReviewFiltersType) {
      const { startDate, endDate, statusList, reviewerLoginList } = args;
      this.start = startDate;
      this.end = endDate;
      this.statusList = statusList;
      this.reviewerLoginList = reviewerLoginList;
      this.options.page = 1;
      this.listFaultyReviews();
    },
  },
  computed: {
    footerProps() {
      return { itemsPerPageOptions: [10, 25, 50, 100] };
    },
    headers: () => [
      {
        text: "Review ID",
        align: "start",
        value: "reviewId",
        sortable: false,
      },
      { text: "Pull Request ID", value: "pullNumber" },
      { text: "Pull Request State", value: "pullRequest.state", sortable: false },
      { text: "Reviewer", value: "reviewerLogin" },
      { text: "Accuracy", value: "accuracy" },
      { text: "Base Branch", value: "pullRequest.base.ref", sortable: false },
      { text: "Head Branch", value: "pullRequest.head.ref", sortable: false },
      { text: "Review Date", value: "submittedAt" },
    ],
  },
});
